.library {
	position: fixed;
	top: 0;
	left: 0;
	width: 20rem;
	height: 100%;
	//   background-color: lightcyan;
	box-shadow: 2px 2px 50px rgb(206, 206, 206);
	overflow: scroll;
	h2 {
		padding: 2rem;
	}
	transform: translateX(-100%);
	transition: all 0.5s ease;
	background-color: white;
}

.library-song {
	display: flex;
	align-items: center;
	padding: 1rem 2rem 1rem 2rem;
	transition: backgound 0.5s ease;
	img {
		width: 30%;
	}
	cursor: pointer;
	&:hover {
		background-color: rgb(203, 206, 255);
	}
}

.song-description {
	padding-left: 1rem;
	h3 {
		font-size: 1rem;
	}
	h4 {
		font-size: 0.7rem;
	}
}

* {
	scrollbar-width: thin;
	scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}

*::-webkit-scrollbar {
	width: 5px;
}

*::-webkit-scrollbar-track {
	background: transparent;
}

*::-webkit-scrollbar-thumb {
	background-color: rgba(155, 155, 155, 0.5);
	border-radius: 20px;
	border: transparent;
}

.selected {
	background-color: rgb(255, 241, 255);
}

.active-library {
	transform: translateX(0%);
	opacity: 1;
}

@media screen and (max-width: 768px) {
	.library {
		width: 100%;
	}
}
